import React, { useState, useEffect } from "react";
const epub = require('epubjs').default;


const get_url = () => {
  const urlSearchParams = new URLSearchParams(window.location.search);
  const params = Object.fromEntries(urlSearchParams.entries());
  const default_epub_url = "https://gerhardsletten.github.io/react-reader/files/alice.epub";
  const epub_url = params.epub_url || default_epub_url;
  return epub_url;
};


const SectionContents = props => {
    const { section, go_next } = props;
    console.log(section.html);

    useEffect(() => {
        window.scrollTo(0, 0)
    });

    return (
        <div>
            <div dangerouslySetInnerHTML={{__html: section.html.innerHTML}}></div>
            <button onClick={go_next}>Next</button>
        </div>
    );
};


const App = () => {

    const [book_contents, set_book_contents] = useState(null);
    const [current_section_idx, set_current_section_idx] = useState(null);

    useEffect(() => {
        const load_book_contents = async () => {
            const book = epub(get_url());
            await book.ready;
            const meta = book.package.metadata;
            const book_title = meta.title;

            document.title = book_title;

            const flat_toc = (function flatten(items) {
                return [].concat.apply([], items.map(item => [].concat.apply(
                    [item],
                    flatten(item.subitems)
                )));
            })(book.navigation.toc);

            const find_section_title = section => {
                const toc_entry = flat_toc.find(e => e.href === section.href);
                return toc_entry ? toc_entry.label : ""
            };

            const spine = await book.loaded.spine;
            const request_fn = book.load.bind(book);
            const sections = spine.spineItems;

            const find_section_idx = href => {
                const idx = sections.findIndex(s => s.href === href);
                return idx >= 0 ? idx : 0;
            };


            const section_array = [];
            for (const section of sections) {
                const html = await section.load(request_fn);
                section_array.push({
                    title: find_section_title(section),
                    html: html
                });
            }

            const contents = {
                title: book_title,
                toc_entries: flat_toc.map(e => ({
                    title: e.label,
                    section_idx: find_section_idx(e.href)
                })), 
                sections: section_array
            };

            if (contents.sections.length > 0) {
                set_book_contents(contents);
                set_current_section_idx(0);
            }
        };

        load_book_contents();
    }, []);

    return book_contents !== null && current_section_idx !== null ?
        (
            <SectionContents
                section={book_contents.sections[current_section_idx]}
                go_next={() => {
                    const next_section_idx = (current_section_idx + 1) % book_contents.sections.length;
                    set_current_section_idx(next_section_idx);
                }}
            />
        )
        :
        (
            <div>Loading...</div>
        );
}

export default App;
